@import 'mixins';

.menu {
  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;
  overflow: auto;

  &___content {
    @include content-padding-inline;
  }

  &__top {
    margin-bottom: 32px;
    background-color: #c2c2c2;
    position: sticky;
    top: 0;
    z-index: 1;
  }

  &-phone {
    color: #1d1d1d;
    text-decoration: none;
    display: block;
    font-size: 16px;
    line-height: 27px;
    margin-bottom: 16px;
  }

  &__nav {
    margin-bottom: 40px;
  }
}
